.live-in-israel {
  margin-bottom: 93px;
}

.live-in-israel__wrapper {
  padding-bottom: 64px;
  padding-left: 104px;
  padding-right: 110px;
  margin-top: -141px;
  background-position: left -46px, right 610px;
  background-repeat: no-repeat;
  background-color: transparent;
  background-image: url("../img/bg-live-desktop@1x.png"),
    url("../img/bg-watch-desktop@1x.png");

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-size: cover;
    background-image: url("../img/bg-live-desktop@2x.png");
  }
}

.live-in-israel__text {
  max-width: 720px;
  margin-top: 0;
  margin-bottom: 14px;
  margin-left: auto;
  margin-right: auto;
  font-size: 17px;
  line-height: 29px;
  text-align: right;
  opacity: 0.87;
}

.live-in-israel__img {
  position: relative;
}

.live-in-israel__img--1 img {
  width: 482px;
  height: 732px;
}

.live-in-israel__img--2 img {
  width: 211px;
  height: 318px;
}

.live-in-israel__img--3 img {
  width: 411px;
  height: 318px;
}

.live-in-israel__img--4 img {
  width: 411px;
  height: 318px;
}

.live-in-israel__img--5 img {
  width: 214px;
  height: 318px;
}

.live-in-israel__label {
  font-family: $alternative-2-font-family;
  font-style: normal;
  font-weight: 800;
  font-size: 21px;
  line-height: 25px;
  color: white;
  text-shadow: 0 1px 4px rgba(0, 0, 0, 0.12);
  position: absolute;
  bottom: 35px;
  left: 40px;
}

.live-in-israel__img--1 .live-in-israel__label {
  max-width: 350px;
}

.live-in-israel__img--2 .live-in-israel__label {
  max-width: 120px;
  left: 25px;
}

.live-in-israel__img--3 .live-in-israel__label {
  max-width: 350px;
  left: 33px;
}

.live-in-israel__img--4 .live-in-israel__label {
  max-width: 360px;
  width: 100%;
  left: 34px;
}

.live-in-israel__img--5 .live-in-israel__label {
  max-width: 120px;
  left: 25px;
}

